Name=\"删除\";\r\n            }\r\n            if(this.deleteState===0){\r\n             //   this.btnName=\"\";\r\n            }\r\n            if(this.deleteState===2){\r\n               // this.btnName=\"确认删除\";\r\n\r\n              // deleteFn.call(this);\r\n            }\r\n\r\n                console.log(this.btnName);\r\n\r\n            },\r\n\r\n\r\n\r\n            touchStartFunc:function(e){\r\n                this.moveStart=true;\r\n                console.log(\"touchStartFunc\");\r\n                this.touchPos(e);\r\n                this.startX = this.oPosition.x;\r\n                this. startY =this. oPosition.y;\r\n\r\n            },\r\n              //获取触点位置\r\n                      touchPos:  function (e){\r\n                             if(e.clientX){\r\n\r\n\r\n                                    this.oPosition.x = e.clientX;\r\n                                    this.oPosition.y = e. clientY;\r\n                                    return this.oPosition;\r\n                                }\r\n                            var touches = e.changedTouches, l = touches.length, touch, tagX, tagY;\r\n                            for (var i = 0; i < l; i++) {\r\n                                touch = touches[i];\r\n                                tagX = touch.clientX;\r\n                                tagY = touch.clientY;\r\n                            }\r\n                            this.oPosition.x = tagX;\r\n                            this.oPosition.y = tagY;\r\n                            return this.oPosition;\r\n                        },\r\n        //触摸移动\r\n           touchMoveFunc: function (e){\r\n           if(!this.moveStart){\r\n            return;\r\n           }\r\n          // console.log(\"touchMoveFunc\");\r\n                this.touchPos(e);\r\n                var moveX = this.oPosition.x -  this.startX;\r\n                var moveY = this.oPosition.y -  this.startY;\r\n               // console.log(moveX);\r\n                if (moveX<-20) {\r\n                    e.preventDefault();\r\n                   this.deleteState=1;\r\n                }\r\n                if (moveX>20) {\r\n                                    e.preventDefault();\r\n                                   this.deleteState=0;\r\n                                }\r\n            },\r\n        //触摸结束\r\n        touchEndFunc:function (e){\r\n        this.moveStart=false;\r\n        console.log(\"touchEndFunc\");\r\n        }\r\n        },\r\n\r\n\r\n        events: {\r\n\r\n        }\r\n};\r\n\r\n\r\n\r\n</script>\r\n<style>\r\nli{ list-style: none; }\r\n.zw-app-slid-it{\r\npadding-left:8px;\r\nborder-radius:8px;\r\n/*border:1px solid red;*/\r\nborder-bottom:1px solid #bcadad;\r\n/*min-height:75px;*/\r\ndisplay:flex;\r\n\r\n\r\njustify-content: flex-start ;\r\n align-items: stretch ;\r\nvertical-align:middle;\r\n}\r\n.zw-app-slid-it-pic{\r\nwidth:40px;\r\nmin-width:40px;\r\n/*display:table-cell;*/\r\n}\r\n\r\n.zw-app-slid-it-content{\r\nwidth:80%;\r\nposition:relative;\r\n/*margin-left:3%;*/\r\nline-height:50px;\r\nmin-height:50px;\r\npadding-left:3%;\r\n/*display:table-cell;*/\r\nvertical-align: middle;\r\n}\r\n.zw-app-slid-it-pic img{\r\n\r\nwidth:100%;\r\n/*max-height: 100px;\r\nmin-height: 100px;*/\r\n}\r\n.zw-app-slid-it-text{\r\nwidth:80%;\r\nfont-size:12px;\r\nposition:absolute;\r\nbottom:0px;\r\ncolor:gray;\r\ndisplay:flex;\r\n\r\npadding-right:50px;\r\noverflow:hidden;\r\njustify-content:space-between;\r\n}\r\n.zw-app-slid-it h1 {\r\n    font-size:16px !important;\r\n    line-height:18px;\r\n}\r\n.zw-app-slid-it h2 {\r\n    font-size:12px !important;\r\n    line-height:20px;\r\n}\r\n.zw-app-slid-it-btn{\r\ntransition: width 0.2s;\r\nwhite-space:nowrap;\r\nvertical-align:center;\r\n/*min-height:75px;\r\nline-height:75px;*/\r\ntext-align:center;\r\n   background-color:#f47d7d;\r\n   width:20%;\r\n  /* height:100%;*/\r\n   padding-top:5px;\r\n   color:white;\r\n}\r\n.zw-app-slid-it h3 {\r\n    font-size:11px !important;\r\n}\r\n.zw-app-slid-it-switch{\r\n    padding-right:15px;\r\n    width:75px;\r\n        width:35px;\r\n\r\n\r\n}\r\n.zw-app-slid-it-switch img{\r\n\r\n    height:15px;/**控制图片的展示大小*/\r\n  /**控制水平居中*/\r\n}\r\n.zw-app-slid-it-title{\r\ndisplay:inline-block;\r\npadding-top:12px;\r\n}\r\n.zw-app-slid-it-switch *{\r\n\r\n\r\n\r\n\r\n}\r\n\r\n\r\n .ui-flex {\r\n            display: -webkit-box !important;\r\n            display: -webkit-flex !important;\r\n            display: -ms-flexbox !important;\r\n            display: flex !important;\r\n            -webkit-flex-wrap: wrap;\r\n            -ms-flex-wrap: wrap;\r\n            flex-wrap: wrap\r\n        }\r\n\r\n        .ui-flex, .ui-flex *, .ui-flex :after, .ui-flex :before {\r\n            box-sizing: border-box\r\n        }\r\n\r\n        .ui-flex.justify-center {\r\n            -webkit-box-pack: center;\r\n            -webkit-justify-content: center;\r\n            -ms-flex-pack: center;\r\n            justify-content: center\r\n        }\r\n        .ui-flex.center {\r\n            -webkit-box-pack: center;\r\n            -webkit-justify-content: center;\r\n            -ms-flex-pack: center;\r\n            justify-content: center;\r\n            -webkit-box-align: center;\r\n            -webkit-align-items: center;\r\n            -ms-flex-align: center;\r\n            align-items: center\r\n        }\r\n</style>\r\n"],"sourceRoot":""}]);

